@import url(https://fonts.googleapis.com/css?family=Lato|Open+Sans|Roboto|Stylish&display=swap);
.delete-btn {
  float: right;
  color: #e74944;
}

.jumbotron{
    height: 300;
    clear: both;
    padding-top: 120;
    text-align: center
}
.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

button{
    color: blue;
}
.toolbar{
    width: 100%;
    position: fixed;
    background-color: white;
    height: 76px;
    top: 0;
    left: 0;
    z-index: 10000;
}

.navLogo a{
    font-size: 2rem;
}

.nav_toolbar{
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1rem;

}

.navItem a{
    color: #eec018;
    padding: 5px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 29px;
}
 
.navItem a:hover,
.navItem a:active{
    color: #000080;
}

.navItem ul{
    list-style: none;
    margin: 0; 
    padding: 0;
    display: flex;
}

.navItem il{
    position: relative;
    padding: 5rem;
    
}

.spacer{
    flex: 1 1; 
}

img {
  width: 198px;
}


body{
    margin: 0; 
    padding: 0; 
    font-family: sans-serif;
    background-image: url(/static/media/backgroundOne.406e485b.jpg);
}
.footer{
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    text-align: center;
    color: #000080;
}

.footer span {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 3px;
    background-color: #eec018;
  }
  
